<template>
    <div class="tabs">
        <el-tabs :tab-position="tabPosition" style="height: 100%;" v-model="sanzu">
            <el-tab-pane label="人员管理" name="first">
                <div class="search">
                    <span>用户名：</span>
                    <el-select
                        v-model="value12"
                        multiple
                        collapse-tags
                        filterable
                        style="margin-left: 20px;"
                        @change="yhm"
                        placeholder="请选择">
                        <el-option
                        v-for="item in options2"
                        :key="item.name"
                        :label="item.name"
                        :value="item.name">
                        </el-option>
                    </el-select>
                    <el-button class="btn1" type="success" @click="query2">查询</el-button>
                </div>
                <div class="table2">
                    <el-table
                        :data="tableData2.slice((currpage2 - 1) * pagesize2, currpage2 * pagesize2)"
                        stripe
                        border
                        style="width: 100%">
                        <el-table-column
                        prop="dept_name"
                        label="部门名"
                        align="center"
                        width="">
                        </el-table-column>
                        <el-table-column
                        prop="name"
                        label="名字"
                        align="center"
                        width="">
                        </el-table-column>
                        <el-table-column
                        prop="mobile"
                        label="手机号"
                        align="center"
                        width="">
                        </el-table-column>
                        <el-table-column
                        prop="email"
                        label="邮箱"
                        width="280"
                        align="center">
                        </el-table-column>
                        <el-table-column
                        label="操作"
                        align="center"
                        prop="roles"
                        width="">
                        <template slot-scope="scope">
                            <el-button @click="handleClick12(scope.row)" type="text" size="small">查看</el-button>
                            <el-button 
                            type="text" 
                            size="small" 
                            @click="xiugaiRenyuanFenzu(scope.row)">修改分组</el-button>
                        </template>
                        </el-table-column>
                    </el-table>
                    <div class="block">
                        <el-pagination
                        @size-change="handleSizeChange2"
                        @current-change="handleCurrentChange2"
                        :current-page="currentPage2"
                        :page-sizes="[2, 3, 5, 7]"
                        :page-size="pagesize2"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="total2">
                        </el-pagination>
                    </div>
                </div>
            </el-tab-pane>
            <el-tab-pane label="分组管理" name="second">
                <div class="search">
                    <span>分组名：</span>
                    <el-select
                        v-model="value11"
                        multiple
                        collapse-tags
                        filterable
                        style="margin-left: 20px;"
                        @change="jsm"
                        placeholder="请选择">
                        <el-option
                            v-for="item in options"
                            :key="item.role_id"
                            :label="item.role_name"
                            :value="item.role_id">
                        </el-option>
                    </el-select>
                    <el-button class="btn1" type="success" @click="query">查询</el-button>
                    <el-button type="success" @click="dialogFormVisible = true">添加</el-button>
                </div>
                <div class="table">
                    <el-table
                        :data="tableData.slice((currpage - 1) * pagesize, currpage * pagesize)"
                        stripe
                        border
                        style="width: 100%">
                        <el-table-column
                        prop="role_id"
                        label="分组ID"
                        align="center"
                        width="150">
                        </el-table-column>
                        <el-table-column
                        prop="role_name"
                        label="分组名"
                        align="center"
                        width="">
                        </el-table-column>
                        <el-table-column
                        prop="role_code"
                        label="分组描述"
                        align="center"
                        width="">
                        </el-table-column>
                        <el-table-column
                        label="操作"
                        align="center"
                        width="">
                        <template slot-scope="scope">
                            <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
                            <el-button type="text" size="small" @click="xiugaiRenyuanGuanli(scope.row)">修改</el-button>
                            <el-button type="text" size="small" @click="deleteFenzu(scope.row)">删除</el-button>
                        </template>
                        </el-table-column>
                    </el-table>
                    <div class="block">
                        <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="currentPage4"
                        :page-sizes="[2, 3, 5, 7]"
                        :page-size="pagesize"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="total">
                        </el-pagination>
                    </div>
                </div>
            </el-tab-pane>
            <el-tab-pane label="权限管理" name="third">
                <div style="height:500px;overflow:scroll;">
                    <el-tabs v-model="activeName" @tab-click="handleClick3">
                        <el-tab-pane label="所有权限" name="first">
                            <el-tree class="props" 
                            :data="tree" 
                            :props="defaultProps" 
                            default-expand-all
                            @node-click="handleNodeClick">
                            </el-tree>
                        </el-tab-pane>
                    </el-tabs>
                </div>
            </el-tab-pane>
        </el-tabs>
        <el-dialog title="添加角色" :visible.sync="dialogFormVisible">
            <el-form :model="form">
                <el-form-item label="角色名：" :label-width="formLabelWidth">
                    <el-input class="input" v-model="form.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="角色描述：" :label-width="formLabelWidth">
                    <el-input class="input" v-model="form.region" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div class="tab">
                <el-tabs v-model="activeName2" type="card" @tab-click="handleClick2">
                    <el-tab-pane label="角色权限项" name="first" class="tree">
                        <el-tree
                            :data="tree003"
                            show-checkbox
                            default-expand-all
                            node-key="permis_id"
                            ref="tree03"
                            highlight-current
                            :props="defaultProps003">
                        </el-tree>
                    </el-tab-pane>
                </el-tabs>
            </div>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="addJuese">确 定</el-button>
            </div>
        </el-dialog>
        
        <el-dialog title="添加组" 
        :visible.sync="dialogFormVisible03"
        :before-close="handleClose">
            <el-form :model="form">
                <el-form-item label="组名：" :label-width="formLabelWidth">
                    <el-input class="input" v-model="name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="组描述：" :label-width="formLabelWidth">
                    <el-input class="input" v-model="miaoshu" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <el-tabs v-model="activeName2" type="card" @tab-click="handleClick2">
                <el-tab-pane label="组权限项" name="first" class="tree">
                    <el-tree
                    :data="tree003"
                    show-checkbox
                    default-expand-all
                    node-key="permis_id"
                    ref="tree04"
                    highlight-current
                    :props="defaultProps003">
                    </el-tree>
                </el-tab-pane>
            </el-tabs>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible03 = false">取 消</el-button>
            <el-button type="primary" @click="confirmXiugai">确 定</el-button>
        </div>
        </el-dialog>
        <el-dialog
            title="提示"
            :visible.sync="dialogVisible"
            width="30%"
            :before-close="handleClose">
            <ul class="handleClose">
                <li>组ID： {{ID}}</li>
                <li>组名： {{name}}</li>
                <li>组描述： {{miaoshu}}</li>
                <li>
                    <span>
                        <span>权限： </span>
                        <ul v-if="index">
                            <li v-for="(item,index) in quanxian" :key="index">{{item.permis_name}}</li>
                        </ul>
                        <ul v-else>
                            <li>无</li>
                        </ul>
                    </span>
                </li>
            </ul>
        </el-dialog>
        
        <el-dialog
        title="提示"
        :visible.sync="deleteKind" 
        width="30%">
        <span>确定要删除该分组吗？</span>
        <span slot="footer" class="dialog-footer">
            <el-button @click="deleteKind = false">取 消</el-button>
            <el-button type="primary" @click="jueseDelete">确 定</el-button>
        </span>
        </el-dialog>

        <el-dialog
            title="详细信息"
            :visible.sync="renyuanChakanVisible"
            width="30%"
            :before-close="handleClose">
            <ul class="handleClose">
                <li>姓名： {{ renyuanChakan.name }}</li>
                <li>职位： {{ renyuanChakan.zw }}</li>
                <li>性别： {{ (renyuanChakan.gender == 1? '男':'女') }}</li>
                <li>手机号： {{ renyuanChakan.mobile }}</li>
                <li>微信号： {{ renyuanChakan.wx_num }}</li>
                <li>邮箱： {{ renyuanChakan.email }}</li>
                <!-- <el-tab-pane label="所在分组" name="first" class="tree"> -->
                    <h3 style="margin-top:10px;">所在分组:</h3>
                    <el-tree 
                        :data="renyuanChakan.roles" 
                        :props="defaultPropsroles"
                        node-key="role_id">
                    </el-tree>
                <!-- </el-tab-pane> -->
            </ul>
        </el-dialog>


        <el-dialog title="更改角色分组" :visible.sync="dialogFormVisible2">
                <el-tabs v-model="activeName3" type="card" @tab-click="handleClick5">
                    <el-tab-pane label="角色权限项" name="first">
                        <el-tree
                            :data="data3"
                            show-checkbox
                            node-key="role_id"
                            ref="treedata3"
                            highlight-current
                            :props="defaultProps2">
                        </el-tree>
                    </el-tab-pane>
                </el-tabs>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible2 = false">取 消</el-button>
                <el-button type="primary" @click="ensureXiugai">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import axios from 'axios';
import { setTimeout } from 'timers';
export default {
    inject:["reload"],
    name:"quanxian",
    data(){
        return{
            data_user_id: null,
            defaultPropsroles:{
                label: 'role_name'
            },
            deleteKind: false,
            deleteFenzuHezi: null,
            sanzu: 'first',
            tree: [],
            tree003: [],
            defaultProps003: {
                children: 'permis',
                label: 'permis_name',
                id: 'permis_id'
            },
            formLabelWidth: '120px',
            renyuanChakan: {},
            renyuanChakanVisible: false,
            tabPosition: 'left',
            currentPage4: 1,
            pagesize:5,
            currpage:1,
            currentPage2: 1,
            pagesize2:5,
            currpage2:1,
            total: null,
            total2: null,
            index:true,
            dialogFormVisible: false,
            dialogFormVisible2: false,
            dialogFormVisible03: false,
            dialogVisible: false,
            activeName: 'first',
            activeName2: 'first',
            activeName3: 'first',
            ID:"",
            name:"",
            miaoshu:"",
            quanxian:[],
            quanxian01:null,
            tableData: [],
            tableData2: [],
            options: [],
            value11: [],
            options2: [],
            value12: [],
            form: {
                name: '',
                region: '',
                date1: '',
                date2: '',
                delivery: false,
                type: [],
                resource: '',
                desc: ''
            },
            defaultProps3: {
                children: 'permis',
                label: 'permis_name'
            },
            data3: [],
            defaultProps2: {
                // children: 'children',
                label: 'role_name'
            },
            defaultProps: {
                children: 'permis',
                label: 'permis_name',
                id:"id"
            }
        }
    },
    methods: {
        ensureXiugai(){    // 人员修改时的确定功能
            var zxc01 = this.$refs.treedata3.getCheckedKeys().toString();
            var that = this;
            $.ajax({
                type: 'POST',
                url: this.baseUrl+"/ydjxzg/ht/updatefz.action",
                data:{
                    user_id: this.data_user_id,
                    role_id: zxc01
                },
                dataType: 'json',
                success: function(res){
                    that.$message.success('修改成功！');
                    that.getUsers();
                },
                error: function(res){
                    that.$message.error('修改失败！');
                }
            })
            // var zxc02 = this.$refs.treedata3.getCheckedNodes();
            // console.log(zxc02)
            this.dialogFormVisible2 = false;
        },
        
        xiugaiRenyuanFenzu(data){  // 点击人员修改按键时的功能
            this.data_user_id = data.user_id;
            this.$nextTick(() => {
                // console.log(this.$refs.tree04)
                var zxc = [];
                for (let q = 0; q < data.roles.length; q++) {
                    zxc.push(data.roles[q].role_id);
                }
                this.$refs.treedata3.setCheckedKeys(zxc);
            });
            console.log(data);
            this.dialogFormVisible2 = true;
        },
        deleteFenzu(data){
            this.deleteKind = true;
            this.deleteFenzuHezi = data;
        },
        jueseDelete() {
          var that = this;
          
          var data = this.deleteFenzuHezi;
          console.log(data, 'shanchu');
          $.ajax({
              type: "POST",
              url: this.baseUrl+"/ydjxzg/ht/deletezu.action",
              data:{
                  role_id: data.role_id
              },
              dataType: "json",
              success: function(res){
                  console.log(res);
                  that.getTabbleData();
                  that.$message.success(res.mess);
                  that.deleteKind = false;
              }
          });
      },
        confirmXiugai(){
            var that = this;
            this.$nextTick(() => {
                var per = this.$refs.tree04.getCheckedKeys();
                console.log(per, 'perper');
                this.quanxian01 = per.toString();
                $.ajax({
                type: 'POST',
                url: this.baseUrl+'/ydjxzg/ht/updatezu.action',
                data: {
                    role_id: this.ID ,
                    role_name: this.name,
                    role_code: this.miaoshu,
                    permis_id: this.quanxian01 || []
                },
                datatype: 'json',
                success: function(res){
                    console.log(res);
                    that.$message.success('修改成功！');
                    that.getTabbleData();
                },
                error: function(res){
                    that.$message.error('修改失败！如有疑问请联系管理员');
                    console.log(res);
                }
            });
            });
            this.dialogFormVisible03 = false;
        },
        xiugaiRenyuanGuanli(data){
            console.log(data);
            this.ID = data.role_id;
            this.name = data.role_name;
            this.miaoshu = data.role_code;
            // var that = this;
            var linshi = [];
            for (let j = 0; j < data.permis.length; j++) {
                var asd = data.permis[j].permis_id
                linshi.push(asd);
            }
            this.$nextTick(() => {
                // console.log(this.$refs.tree04)
                this.$refs.tree04.setCheckedKeys(linshi);
                
            });
            this.dialogFormVisible03 = true;
        },
        
        addJuese(){
            this.dialogFormVisible = false;
            var jueseId = this.$refs.tree03.getCheckedKeys().toString();
            // console.log(jueseId, 'juese');
            // console.log(this.form.name, 'juese');
            // console.log(this.form.region, 're');
            if (this.form.name == ''){
                this.$message.error('角色名不能为空');
                // console.log(this.form.name, 'juese');
                // console.log(this.form.region, 're');
                return false;
            }else if(this.form.region == ''){
                this.$message.error('角色名描述不能为空');
                // console.log(this.form.name, 'juese');
                // console.log(this.form.region, 're');
                return false;
            }
            console.log(this.form.region, 'juese');
            var that = this;
            $.ajax({
                type: "POST",
                url: this.baseUrl + "/ydjxzg/ht/insertzu.action",
                data:{
                    role_name: this.form.name,
                    role_code: this.form.region,
                    permis_id: jueseId
                },
                dataType: 'json',
                success: function(res){
                    that.form.name = '';
                    that.form.region = '';
                    that.$message.success(res.mess);
                    that.getTabbleData();
                }
            })
        },
      handleClick(data) {

        console.log(data);
        this.ID = data.role_id;
        this.name = data.role_name;
        this.miaoshu = data.role_code;
        this.quanxian = data.permis
        if(data.permis.length == 0){
            this.index = false;
        }else{
            this.index = true;
        }
        this.dialogVisible = true;
      },
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
        this.pagesize = val
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
        this.currpage = val
      },
      handleSizeChange2(val) {
        console.log(`每页 ${val} 条`);
        this.pagesize2 = val
      },
      handleCurrentChange2(val) {
        console.log(`当前页: ${val}`);
        this.currpage2 = val
      },
      jsm(e){
          console.log(e)
          localStorage.setItem("e",e)
      },
      yhm(e){
          console.log(e)
          localStorage.setItem("se",e)
      },
      handleClick2(tab, event) {
        console.log(tab, event);
      },
      handleClick3(tab, event) {
        console.log(tab, event);
      },
      handleClick5(tab, event) {
        console.log(tab, event);
      },
      handleClick12(tab) {
        console.log(tab);
        this.renyuanChakan = tab;
        this.renyuanChakanVisible = true;
      },
      handleNodeClick(data) {
        console.log(data);
      },
      
      query(){
          let e = localStorage.getItem("e");
          console.log(e)
      },
      query2(){
          let se = localStorage.getItem("se");
          console.log(se)
      },
      handleClose(done) {   // 已解决
        // this.$confirm('确认关闭？')
        //   .then(_ => {
            done();
        //   })
        //   .catch(_ => {});
      },
      getUsers(){
        var that = this;
        axios.post(this.baseUrl+'/ydjxzg/ht/getUsers.action')
        .then(function (res) {
            console.log(res);
            that.tableData2 = res.data.users;
            that.total2 = res.data.users.length
            that.options2 = res.data.users;
            // console.log(res.data.users, '想要的');
        });
        },
    getTreeList(){
        var that = this;
        axios.post(this.baseUrl+'/ydjxzg/ht/selectpermis.action')
            .then(function (res) {
                console.log(res, 'gettreelist');
                that.tree = res.data.permis
                that.tree003 = res.data.permis
                    // for(var i=0;i<res.data.permis.length;i++){
                    //     that.tree = res.data.permis[i].permis_name   
                    // }
            });
    },
    getTabbleData(){
        var that = this;
        axios.post(this.baseUrl+'/ydjxzg/ht/sleroles.action')
            .then(function (res) {
                console.log(res, 'getTabbleData');
                that.tableData = res.data.role;
                that.options = res.data.role;
                that.data3 = res.data.role;
                that.total = res.data.role.length;
            });
        
    }
     
    },
    created(){
        this.getUsers();
        this.getTreeList();
        this.getTabbleData();
    }
    // watch:{
    //     dialogVisible: function(val, oldval){
    //         var that = this;
    //         if (val == false) {
    //             // that.reload();
    //             that.getTabbleData();
    //         }
    //     }
    // }
}
</script>

<style scoped>
.table2[data-v-3630df82]{
    width: 80%!important;
    margin-right: 0!important;
}
.tabs{
    margin: 30px 0 0 15px;
}
.search{
    margin-bottom: 15px;
}
.btn1{
    margin-left: 10px;
}
.table{
    width: 70%;
}
.table2{
    width: 50%;
}
.block{
    margin-top: 15px;
    float: right;
}
/* .input{
    width: 660px;
} */
.tab{
    /* width: 708px;
    margin-left: 40px; */
    border: 1px solid #cccccc;
    padding: 15px;
}
.tree{
    height: 260px;
    overflow-x:auto;
    white-space:nowrap;
}
.props{
    margin-left: 90px;
}
.handleClose li{
    margin-top: 10px;
}
</style>



